import {multicast, refCount} from 'rxjs/operators';
import {Observable, ReplaySubject} from 'rxjs';

/**
 * Compartilha um observable como `share()`, mas repetindo seu ltimo valor
 * caso o observable j esteja assinado.
 *
 * Caso todas as assinaturas sejam canceladas, o observable tambm deixa de ser
 * assinado, e o valor em cache  descartado.
 */
export function shareLast<T>() {
  // Soluo baseada nas observaes de:
  // https://github.com/ReactiveX/rxjs/issues/3336#issuecomment-391026380
  return (observable: Observable<T>) => observable.pipe(
    multicast(() => new ReplaySubject<T>(1)),
    refCount(),
  );
}
